import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import localFont from 'next/font/local';
import { ThemeProvider } from "./providers/ThemeProvider";
import Header from "./components/layout/Header";
import ProfileSection from "./components/shared/ProfileSection";
import AnimatedBackground from "./components/shared/AnimatedBackground";
import "./globals.css";

const geistSans = Geist({
  subsets: ["latin"],
  variable: "--font-geist-sans",
});

const geistMono = Geist_Mono({
  subsets: ["latin"],
  variable: "--font-geist-mono",
});

// Arabic font (Harmattan) - Updated path
const harmattanArabic = localFont({
  src: './fonts/arabic/Harmattan-Regular.ttf', // Remove '../public' since public is the root
  variable: '--font-arabic',
  display: 'swap',
});

// Use Google Fonts for Playfair Display instead of local file
import { Playfair_Display } from 'next/font/google';

const playfair = Playfair_Display({
  subsets: ['latin'],
  variable: '--font-english',
});

export const metadata: Metadata = {
  title: "Felix Arash - Personal Website",
  description: "Personal blog, music, videos, photos and writings by Felix Arash",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en" data-theme="dark" suppressHydrationWarning>
      <body className={`${geistSans.variable} ${geistMono.variable} ${harmattanArabic.variable} ${playfair.variable} antialiased min-h-screen flex flex-col`}>
        <ThemeProvider>
          <AnimatedBackground />
          <Header />
          <ProfileSection />
          {children}
        </ThemeProvider>
      </body>
    </html>
  );
}